@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
*{outline-width: 0px; border: none;}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#homepage{padding:20px;padding-bottom:0px;min-height:100vh;background:url(/course-problem-deck/static/media/background.5023a7e0.jpg) no-repeat center center fixed;background-size:cover;font-family:'Libre Baskerville', serif}#homepage .header{text-align:center;color:white}#homepage .header h1{margin-bottom:0px}#homepage .course{display:flex;flex-direction:column;flex-wrap:wrap;margin:50px auto}#homepage .course>div{box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12);opacity:0.8;width:50vw;margin:10px auto;padding:20px;background:white;border-radius:5px}#homepage .course .course-content h1{margin:0px;padding:0px;font-size:25px}#homepage .course .course-content .instructor{margin:0px;padding:0px;font-size:15px}#homepage .course .course-content .subtitle{margin-bottom:30px}#homepage .course .course-content a{padding:10px;text-decoration:none;color:white;border-radius:5px}#homepage .course .course-content .browse-course{background:#17a2b8;margin-right:10px}#homepage .course .course-content .browse-problems{background:#28a745}#homepage .fotter{text-align:center;color:white;font-size:20px;margin-bottom:10px}#homepage .fotter a{color:white !important;text-decoration:none}#homepage .fotter span{color:red}

.fotter{text-align:center;color:white;font-size:20px;padding:10px}.fotter a{color:white !important;text-decoration:none}.fotter span{color:red}

#course-page{padding:20px;padding-bottom:0px;background:url(/course-problem-deck/static/media/background.5023a7e0.jpg) no-repeat center center fixed;background-size:cover;min-height:100vh;font-family:'Libre Baskerville', serif}#course-page .header{text-align:center;color:white}#course-page .header h1{margin-bottom:0px;text-transform:uppercase}#course-page .problems .problem{background-color:white;padding:10px;margin:10px;opacity:0.8}#course-page .problems .problem span{color:#155724 !important;font-size:20px;margin-right:10px}#course-page .problems .problem:hover{background-color:honeydew}#course-page .problems .problem-title{color:black;cursor:pointer;text-decoration:none !important;font-size:20px}#course-page .problems .problem-title:hover{color:#1BA94C}#course-page .problems .filter-box{background-color:white;padding:10px;margin:10px;opacity:0.8}#course-page .problems .filter-input{font-size:20px;width:100%;font-family:'Libre Baskerville', serif}#course-page .problems .filter-item{margin-right:10px;margin-bottom:10px;border:1px solid black;border-radius:10px;padding:5px;display:inline-block}#course-page .problems .filter-item>button{background-color:transparent;cursor:pointer}#course-page .error-message{font-size:20px;text-align:center;color:#f8d7da;margin:50px;padding:10px}#course-page .empty-message{font-size:20px;color:white;text-align:center;margin:50px auto}

#loader1{text-align:center;height:300px;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;color:white;font-size:20px}#loader1 .lds-facebook{display:block;position:relative;width:100px;height:100px}#loader1 .lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:#fff;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}#loader1 .lds-facebook div:nth-child(1){left:8px;animation-delay:-0.24s}#loader1 .lds-facebook div:nth-child(2){left:32px;animation-delay:-0.12s}#loader1 .lds-facebook div:nth-child(3){left:56px;animation-delay:0}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}

#login{padding:20px 50px;font-family:'Libre Baskerville', serif}#login .flash-message{display:flex;align-items:center;justify-content:space-between;padding:5px;border-radius:5px}#login .flash-message button{border:none;background:transparent}#login .error{color:#842029;background:#f5c2c7}#login .error button{color:#842029}#login .success{color:#0f5132;background:#badbcc}#login .success button{color:#0f5132}#login .form-group{margin:20px auto}#login .form-group label{display:block}#login .form-group input{border:1px solid black;width:100%;padding:10px;font-size:20px;border-radius:10px}#login button{padding:10px;font-size:20px;cursor:pointer;background:#6c757d;color:white;border-radius:10px}

.loader2 .lds-circle{display:inline-block;transform:translateZ(1px)}.loader2 .lds-circle>div{display:inline-block;width:64px;height:64px;margin:8px;border-radius:50%;background:#6c757d;animation:lds-circle 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite}@keyframes lds-circle{0%,100%{animation-timing-function:cubic-bezier(0.5, 0, 1, 0.5)}0%{transform:rotateY(0deg)}50%{transform:rotateY(1800deg);animation-timing-function:cubic-bezier(0, 0.5, 0.5, 1)}100%{transform:rotateY(3600deg)}}

#attempt-problem{-ms-user-select:none;user-select:none;-webkit-user-select:none;display:flex;justify-content:space-between;overflow:hidden;height:100vh;width:100vw}#attempt-problem>div{height:100vh;overflow:scroll}#attempt-problem .resizer{width:5px !important;cursor:ew-resize;border:2px solid transparent;overflow:hidden !important}#attempt-problem .left,#attempt-problem .right{min-width:30vw;width:50vw}#attempt-problem .full-flex{flex:1 1}#attempt-problem .left{font-family:'Libre Baskerville', serif;padding:10px}#attempt-problem .left a{color:black !important}#attempt-problem .left .problem-header{font-weight:bold;font-size:30px}#attempt-problem .left .problem-score{text-transform:uppercase}#attempt-problem .left .tags .tags-header{font-size:18px;font-weight:bold}#attempt-problem .left .tags .tag{display:inline-block;background-color:whitesmoke;border-radius:10px;padding:5px;margin:5px 5px 0px 0px}#attempt-problem .problem{margin:50px auto}#attempt-problem .problem .ql-toolbar{display:none !important}#attempt-problem .problem .ql-container.ql-snow{border:none !important;font-family:'Libre Baskerville', serif}#attempt-problem .problem .ql-editor{padding:0px !important}#attempt-problem .problem pre.ql-syntax{color:black !important;background-color:white !important;border:1px solid darkgray}#attempt-problem .right{flex:1 1}#attempt-problem .right .code-operations{text-align:right;font-family:'Libre Baskerville', serif;font-size:18px;margin:10px}#attempt-problem .right .code-operations>button{font-size:18px;background:transparent;font-family:'Libre Baskerville', serif;cursor:pointer}#attempt-problem .right .code-operations .submit-code{background:#28a745;color:white;border-radius:5px;padding:10px}#attempt-problem .right .custom-test{font-family:'Libre Baskerville', serif;font-size:15px;padding:10px;padding-left:0px;margin:5px;margin-left:0px}#attempt-problem .right .custom-test>textarea,#attempt-problem .right .custom-test>input{border:1px solid black;border-radius:5px;width:95%;padding:10px;font-family:'Libre Baskerville', serif;font-size:15px;margin:5px auto}#attempt-problem .right .custom-test>textarea{height:100px}#attempt-problem .right .custom-test textarea:focus,#attempt-problem .right .custom-test input:focus{border-color:#9ecaed;box-shadow:0 0 2px #9ecaed}#attempt-problem .right .results{border:1px solid black;padding:5px;display:flex}#attempt-problem .right .results .test-case-list .test-case{background:whitesmoke;padding:5px;cursor:pointer;font-family:'Libre Baskerville', serif;font-size:18px;min-width:200px}#attempt-problem .right .results .test-case-list .selected-test-case{background:white;box-shadow:0 2px 2px 2px gainsboro;-webkit-box-shadow:0 2px 2px 2px gainsboro}#attempt-problem .right .results .test-case-result{flex:1 1;font-family:'Libre Baskerville', serif;font-size:18px;padding:0px 10px 10px 10px}#attempt-problem .right .results .test-case-result textarea{font-family:'Libre Baskerville', serif;font-size:15px;width:100%;background:whitesmoke;padding:5px;overflow:scroll;resize:none}

.loader0{background:#2c3e50;height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;flex-direction:column;font-family:'Libre Baskerville', serif}.loader0 .sk-cube-grid{width:60px;height:60px;margin:0px auto}.loader0 .sk-cube-grid .sk-cube{width:33%;height:33%;background-color:white;float:left;animation:sk-cubeGridScaleDelay 1.3s infinite ease-in-out}.loader0 .sk-cube-grid .sk-cube1{animation-delay:0.2s}.loader0 .sk-cube-grid .sk-cube2{animation-delay:0.3s}.loader0 .sk-cube-grid .sk-cube3{animation-delay:0.4s}.loader0 .sk-cube-grid .sk-cube4{animation-delay:0.1s}.loader0 .sk-cube-grid .sk-cube5{animation-delay:0.2s}.loader0 .sk-cube-grid .sk-cube6{animation-delay:0.3s}.loader0 .sk-cube-grid .sk-cube7{animation-delay:0s}.loader0 .sk-cube-grid .sk-cube8{animation-delay:0.1s}.loader0 .sk-cube-grid .sk-cube9{animation-delay:0.2s}@keyframes sk-cubeGridScaleDelay{0%,70%,100%{transform:scale3D(1, 1, 1)}35%{transform:scale3D(0, 0, 1)}}.loader0 .message{color:white;text-align:center;margin-top:20px;font-size:20px}.loader0 .message a{color:white !important;text-decoration:none}

.code-editor #ace-editor{font-size:20px !important;width:auto !important;height:350px !important}.code-editor .toolbar{padding:5px;text-align:right}.code-editor .toolbar select{font-size:20px;background:white;border:1px solid black;font-family:'Libre Baskerville', serif;margin-left:10px}

